.volume-control {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.8rem;
    background-color: #787f8b;
    padding: 0.6rem 1.2rem;
    border-radius: 50px;
    color: white;
  //  margin-bottom: 1rem;
    
    button {
      background: transparent;
      border: none;
      color: white;
      cursor: pointer;
      display: flex;
      align-items: center;
      padding: 0.1rem;
      font-size: 1.5rem; /* Increase the button size */
    }
    
    input[type="range"] {
      width: 150px;
      height: 5px;
      cursor: pointer;
      appearance: none;
      background: #bcb5db;
      border-radius: 5px;
    
      &::-webkit-slider-thumb {
        appearance: none;
        width: 12px;
        height: 12px;
        background: #6302ff;
        border-radius: 50%;
        cursor: pointer;
        border: none;
      }
    
      &::-moz-range-thumb {
        width: 12px;
        height: 12px;
        background: #5309ff;
        border: none;
        border-radius: 50%;
        cursor: pointer;
      }
    }
  }
  