/* FileName: player.scss */

.player {
    min-height: 12vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 100%; /* Ensure player width is 100% of the container */
    overflow: hidden; /* Prevent horizontal scrollbars */
  }
  
  .time-control {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center; /* Center content */
    padding: 0 1rem; /* Add some padding for better spacing */
    
    input {
      width: 100%;
      background-color: transparent;
      cursor: pointer;
      border: none; /* Remove borders */
      outline: none; /* Remove outline */
    }
    
    p {
      font-weight: 700;
    }
  }
  
  .play-control {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 30%;
   // padding: 1rem; /* Add horizontal padding */
    
    svg {
      cursor: pointer;
    }
  }
  
  input[type="range"]:focus {
    outline: none;
  }
  
  input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 16px;
    width: 16px;
    background: #6302ff; /* Add color to the slider thumb for visibility */
  }
  
  .track {
    background: rgb(151, 205, 223);
    width: 100%;
    height: 1rem;
    position: relative;
    border-radius: 1rem;
    overflow: hidden;
  }
  
  .animate-track {
    background: rgb(238, 209, 209);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateX(0%);
    pointer-events: none;
  }
  
  @media screen and (max-width: 768px) {
    .time-control {
      width: 90%; /* Adjust width for smaller screens */
    }
    
    .play-control {
      width: 80%; /* Adjust play control width for smaller screens */
      padding-bottom: 12px;
    }
  }
  