.library {
    position: fixed;
    top: 0;
    left: 0;
    width: 20rem;
    height: 100%;
    background: #0393e6;
    box-shadow: 2px 2px 50px rgba(0, 0, 0, 0.205);
    overflow-y: auto; /* Allow vertical scrolling */
    transform: translateX(-100%); /* Slide-in/out animation */
    transition: all 0.2s ease;
    opacity: 0;
    z-index: 1000; /* Ensure it's above other components */
    padding-top: 2rem; /* Space out content from top */
    
    h2 {
        padding-left: 2rem;
        color: white;
    }
}

.library-song {
    display: flex;
    align-items: center;
    padding: 1rem 1rem;
    cursor: pointer;
    
    img {
        width: 30%;
        height: auto; /* Ensure image maintains aspect ratio */
        margin-right: 1rem; /* Space between image and text */
    }

    &:hover {
        background: rgb(123, 128, 172);
    }
}

.song-description {
    
    padding-left: 0.5rem;
    h3 {
       

        color: #ffffff;
        font-size: 1rem;
    }
    h4 {
        color: rgb(155, 111, 111);
        font-size: 0.7rem;
    }
}

::-webkit-scrollbar {
    width: 6px; /* Slightly wider scrollbar */
}

::-webkit-scrollbar-thumb {
    background: rgb(224, 0, 0);
    border-radius: 10px;
}

::-webkit-scrollbar-track {
    background: rgb(221, 221, 221);
}

.selected {
    background: rgb(255, 230, 255);
    
    h3 {
        color: #306b26;
    }
}

.active {
    transform: translateX(0%); /* Slide in */
    opacity: 1;
}
.close-button {
    position: absolute;
    top: 1rem;
    right: 1rem;
    background: none;
    border: none;
    font-size: 2rem;
    color: white;
    cursor: pointer;
    z-index: 1001;
    transition: transform 0.2s ease;
}

.close-button:hover {
    transform: scale(1.2);
    color: #ffaaaa;
}


